$color-button: #d12d35;

.button {
  font-family: git commit -m 'add task solution' 'Cinzel';
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  text-align: center;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;

  width: 280px;
  height: 56px;
  background-color: $color-button;
  color: #fff;
  border: none; /* Видаляє стандартну рамку */
  cursor: pointer; /* Змінює курсор на "руку" при наведенні */
  transition: all 0.3s ease; /* Додає плавний перехід для ефектів наведення */

  @include onTablet {
    width: 392px;
    grid-column: 2 / 6;
  }

  @include onDesktop {
    width: 324px;
    grid-column: 5 / 9;
  }
}

button:hover {
  background-color: #b1242e; /* Зміна фону при наведенні */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Додає тінь */
  transform: scale(1.05); /* Легке збільшення кнопки */
}

.button-section {
  @include pageGrid;
  margin-bottom: 16px;
}
