.more-explore-online {
  &__products {
    @include pageGrid;
    row-gap: 40px;
    text-decoration: none;
  }

  &__product {
    text-decoration: none;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2 / -2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }
}
