.icon {
  display: block;
  height: 24px;
  width: 24px;

  @include hover(transform, scale(1.2));

  @include onTablet {
    height: 27px;
    width: 27px;
  }

  &--phone {
    background-image: url(../images/icons/icon-phone.svg);
  }

  &--menu {
    background-image: url(../images/icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../images/icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../images/icons/Icon-Close.svg);
  }
}

.tooltip {
  display: none;

  @include onDesktop {
    font-family: 'Cinzel';
    font-weight: 700;
    line-height: 27px;
    text-align: right;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    color: #fff;
    z-index: 10;
    text-decoration: none;

    width: 400px;
    height: 0px;
    font-size: 14px;
    display: none;
    position: absolute;
    top: -100%;
    right: 100%;
    transition: color 0.3s ease;
  }
}

/* Показ тексту при наведенні */
.top-bar__icon-phone:hover .tooltip {
  @include onDesktop {
    display: block;
  }
}
