$text-title-color: #d12d35;
$text-color: #161616;

.product {
  text-align: center;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  text-decoration: none;

  &__photo {
    width: 100%;
    margin-top: 40px;
  }

  &__title {
    font-family: 'Cinzel';
    font-size: 24px;
    font-weight: 700;
    line-height: 33.6px;
    color: $text-title-color;
    margin: 27 0 0 0;
    text-decoration: none;
  }

  &__information {
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
    color: $text-color;
    margin: 0 0 0 0;
    text-decoration: none;
  }
}
