.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #d12d35;
  font-weight: 700;
  text-transform: uppercase;
  z-index: 1000;
  overflow: auto;

  &__top {
    margin-bottom: 32px;
    position: sticky; /* фіксує кнопку закриття меню */
    top: 0;
    z-index: 1;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 27px;
    color: #fff;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid #fff;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: #fff;
    text-decoration: none;
  }
}
